import urls from '../../../utils/urls.js'
import wxInfoDialog from '../../components/wxInfoDialog.vue'
import sharePop from '../../components/sharePop.vue'
import share from "../../../utils/share.js";
export default {
	components: {
		wxInfoDialog,
		sharePop
	},
	data() {
		return {
			showShare: false,
			story: {},
			isLess: true,
			comment: [], // 所有评价
			comments: [], // 展示的评价列表
			other: [],
			others: [], // 作者其他作品
			animTitle: {},
			current: 0,
			similar: [],
			similars: [],
			isTop: false,
			showDialog: false,
			share_url: '',
			share_title: '',
			share_content:'',
			advs:{},
			isLogin: this.$store.state.isLogin,
			newest:[],
		}
	},
	onLoad(options) {
		let id = options.id
		console.log(id)
		let user = uni.getStorageSync("userInfo")
		console.log(user)
		let uid = ''
		if (user) {
			uid = JSON.parse(user).id
		}
		console.log(id + '++++++' + uid)
		uni.request({
			url: urls.HTTP + '/novel/getnovel',
			method: 'POST',
			data: {
				novelid: id,
				uid: uid
			},
			success: (res) => {
				console.log(res);
				//console.log('书籍详情', res.data)
				this.story = res.data.novel[0]
				this.share_url = this.story.url,
				this.share_title = this.story.title,
				this.share_content = this.story.ncontent,
				this.comment = res.data.comment
				this.comments = res.data.comment.slice(0, 3)
				this.other = res.data.othernovel
				this.others = res.data.othernovel.slice(0, 4)
				this.newest = res.data.newest
				this.similar = res.data.similarnovel
				this.similars = res.data.similarnovel.slice(0, 4)
				this.$store.dispatch('comments', res.data.comment)
				this.$store.dispatch('story', this.story)
			}
		})
		this.adv()
	},
	onBackPress() {
		//监听back键，关闭弹出菜单
		if (this.shareObj.shareMenu.isVisible()) {
			this.shareObj.shareMenu.hide();
			this.shareObj.alphaBg.hide();
			return true
		}
	},
	onShow() {
		var animation = uni.createAnimation({
			duration: 1000
		})
		this.animation = animation
		let id = this.$store.state.bookId
		// let uid = JSON.parse(uni.getStorageSync("userInfo")).id
		let user = uni.getStorageSync("userInfo");
		let uid = ""
		if (user) {
			uid = JSON.parse(user).id
		}			
		console.log(id);
		uni.request({
			url: urls.HTTP + '/novel/getnovel',
			method: 'POST',
			data: {
				novelid: id,
				uid: uid
			},
			success: (res) => {
				console.log(res);
				// console.log('书籍详情', res.data)
				this.story = res.data.novel[0]
				this.comment = res.data.comment
				this.comments = res.data.comment.slice(0, 3)
				this.other = res.data.othernovel
				this.others = res.data.othernovel.slice(0, 4)
				this.newest = res.data.newest					
				this.similar = res.data.similarnovel
				this.similars = res.data.similarnovel.slice(0, 4)
				this.$store.dispatch('comments', res.data.comment)
				this.$store.dispatch('story', this.story)
			}
		})
	},
	onShareAppMessage() {
		let novelid = this.story.source_id
		return {
			title: this.story.content,
			path: '/pages/common/book/storyDetail?id=' + novelid,
			imageUrl: this.story.pic
		}
	},
	onPageScroll(res) {
		if (res.scrollTop > 20) {
			this.animation.opacity(1).step()
			this.animTitle = this.animation.export()
		} else if (res.scrollTop < 20) {
			this.animation.opacity(0).step()
			this.animTitle = this.animation.export()
		} 
	},
	methods: {
		adv() {
			let _this = this
			uni.request({
				url: urls.HTTP + '/index/appad',
				method: 'POST',
				data:{id:7},
				success: (res) => {
					console.log(res);
					if(res.data.code == 0 && res.data.advertisement[0].status == 1){
						res.data.advertisement[0].picpath = urls.ImgUrl + res.data.advertisement[0].picpath,
						_this.advs = res.data.advertisement[0]
					}else{
						_this.advs = false
					}
				}
			})
		},
		shareInfo() {
			let shareInfo = {
				href: this.share_url,
				title: this.share_title,
				desc: this.share_content
			};
			let shareList = [{
					icon: "/static/sharemenu/wx.png",
					text: "微信好友",
				},
				{
					icon: "/static/sharemenu/pyq.png",
					text: "朋友圈"
				},
				// {
				// 	icon: "/static/sharemenu/weibo.png",
				// 	text: "微博"
				// },
				{
					icon: "/static/sharemenu/qq.png",
					text: "QQ"
				},
				{
					icon: "/static/sharemenu/copy.png",
					text: "复制"
				},
				{
					icon: "/static/sharemenu/more.png",
					text: "更多"
				},
			];
			this.shareObj = share(shareInfo, shareList, function(index) {
				console.log("点击按钮的序号: " + index);
				let shareObj = {
					href: shareInfo.href || "",
					title: shareInfo.title || "",
					summary: shareInfo.desc || "",
					success: (res) => {
						let user = uni.getStorageSync("userInfo");
						let uid = ""
						if (user) {
							uid = JSON.parse(user).id
						}else{
							uid=0
						}
						uni.request({
							url: urls.HTTP + '/share/novelshare',
							method: 'POST',
							data: {
								uid: uid
							},
							success: (res) => {
								//console.log(res)
							}
						})
						console.log("success:" + JSON.stringify(res));
					},
					fail: (err) => {
						console.log("fail:" + JSON.stringify(err));
					}
				};
				switch (index) {
					case 0:
						shareObj.provider = "weixin";
						shareObj.scene = "WXSceneSession";
						shareObj.type = 0;
						shareObj.imageUrl = shareInfo.imgUrl || "";
						uni.share(shareObj);
						break;
					case 1:
						shareObj.provider = "weixin";
						shareObj.scene = "WXSenceTimeline";
						shareObj.type = 0;
						shareObj.imageUrl = shareInfo.imgUrl || "";
						uni.share(shareObj);
						break;
					// case 2:
					// 	shareObj.provider = "sinaweibo";
					// 	shareObj.type = 0;
					// 	shareObj.imageUrl = shareInfo.imgUrl || "";
					// 	uni.share(shareObj);
					// 	break;
					case 2:
						shareObj.provider = "qq";
						shareObj.type = 1;
						shareObj.imageUrl = shareInfo.imgUrl || "";
						uni.share(shareObj);
						break;
					case 3:
						uni.setClipboardData({
							data: shareInfo.href,
							complete() {
								uni.showToast({
									title: "已复制到剪贴板"
								})
							}
						})
						break;
					case 4:
						plus.share.sendWithSystem({
							type: "web",
							title: shareInfo.title || "",
							thumbs: [shareInfo.imgUrl || ""],
							href: shareInfo.href || "",
							content: shareInfo.desc || "",
						})
						break;
				};
			});
			this.$nextTick(() => {
				this.shareObj.alphaBg.show();
				this.shareObj.shareMenu.show();
			})
		},
		closepop: function(val) {
			this.showShare = false
		},
		seeTa: function(item) {
			// console.log(item)
			this.$store.dispatch('otherUser', item.user)
			uni.navigateTo({
				url: 'otherUserInfo'
			})
		},
		showMore: function() {
			this.isLess = !this.isLess
		},
		editReview: function() {
			uni.navigateTo({
				url: 'editReview'
			})
		},
		closeDialog: function(val) {
			this.showDialog = val
		},
		goStory: function(item) {
		  var bookId = this.$store.state.story.source_id
		  var key = item
			let id = '';
			if(this.isLogin){
				id = JSON.parse(uni.getStorageSync('userInfo')).id
			}else{
				 id = 0
			}
		 
		  uni.request({
		    url: urls.HTTP + '/novel/getnovelcontent',
		    method: 'POST',
		    data: {
		      id: bookId,
		      key: key,
		      uid: id
		    },
		    success: (res) => {
					
		      // console.log(res)
		      this.$store.dispatch('storyData', res.data)
		      uni.redirectTo({
		        url: 'story'
		      })
		    }
		  })
		},
		readStory: function() {
			let bookId = this.story.id

			let user = uni.getStorageSync("userInfo");
			let uid = ""
			if (user) {
				uid = JSON.parse(user).id
			}
			// if(this.isLogin){
			// 	uid = JSON.parse(uni.getStorageSync('userInfo')).id
			// }else{
			// 	 uid = 0
			// }
			uni.request({
				url: urls.HTTP + '/novel/readnovel',
				method: 'POST',
				data: {
					novel_id: bookId,
					uid: uid
				},
				success: (res) => {
					// console.log(res)
					this.$store.dispatch('storyData', res.data)
					uni.redirectTo({
						url: 'story'
					})
				}
			})
		},
		goChapters: function() {
			this.$store.dispatch('pageTitle', this.story.title)
			uni.navigateTo({
				url: 'chapters'
			})
		},
		addToShelf: function() {
			let novelid = this.story.id
			if (this.$store.state.isLogin == false) {
				uni.showModal({
					title: '提示',
					content: '您还没有登录，是否登录？',
					success: (res) => {
						if (res.confirm) {
							uni.navigateTo({
								url: '../login'
							})
						}
					}
				})
			} else {
				// let id=JSON.parse(uni.getStorageSync("userInfo")).id
				let user = uni.getStorageSync("userInfo");
				let id = ""
				if (user) {
					id = JSON.parse(user).id
				}
				console.log(id)
				uni.request({
					url: urls.HTTP + '/Bookshelf/addbookshelf',
					method: 'POST',
					data: {
						uid: id,
						novelid: novelid
					},
					success: (res) => {
						// console.log('添加书架', res)
						if (res.data.code === 0) {
							this.story.isbookshelf = 0
							uni.showToast({
								title: '已成功加入书架',
								icon: 'none'
							})
						} else {
							uni.showToast({
								title: res.data.error
							})
						}
					}
				})
			}
		},
		goDetail: function(val) {
			console.log(val)
			this.$store.dispatch('comment', val)
			uni.navigateTo({
				url: 'commontDetail'
			})
		},
		thumbsUp: function(item) {
			// console.log(item)
			var id = item.id
			// var uid = JSON.parse(uni.getStorageSync("userInfo")).id
			let user = uni.getStorageSync("userInfo");
			let uid = ""
			if (user) {
				uid = JSON.parse(user).id
			}

			var fuid = item.user.id
			this.current = id
			uni.request({
				url: urls.HTTP + '/comment/upcomment',
				method: 'POST',
				data: {
					cid: id,
					uid: uid,
					fuid: fuid
				},
				success: (res) => {
					// console.log(res)
					if (res.data.code === 0) {
						item.up++
					} else {
						uni.showToast({
							title: '请不要重复点赞',
							icon: 'none'
						})
					}
				}
			})
		},
		hasUp: function(i) {
			if (i === this.current) {
				return true
			} else {
				return false
			}
		},
		goAdv: function(e) {
			this.advShow = false
			clearInterval(this.interval)
			clearTimeout(this.timeOut)
			plus.runtime.openURL(e);
		},
	},
	computed: {
		rate() {
			return Number(this.story.rating)
		},
		isSeeOne() {
			return this.others.length > 4 ? true : false
		},
		id() {
			return uni.getStorageSync('user').id
		},
		isAdd() {
			if (this.story.isbookshelf === 0) {
				return true
			} else {
				return false
			}
		},
		urlHttp() {
			return urls.UrlHttp
		}
	}
}
